   const settingsSlider = {
      infinite: false,
      slidesToShow: 4,
      slidesToScroll: 1,
      dots: true,
      responsive: [
         {
            breakpoint: 1280,
            settings: {
               slidesToShow: 3,
            }
         }, {
            breakpoint: 1024,
            settings: {
               slidesToShow: 2,
               arrows: false,
               variableWidth: true
            }
         }, {
            breakpoint: 768,
            settings: {
               slidesToShow: 1,
               arrows: false,
               variableWidth: true
            }
         }
      ]
   }

   const handleChangePlan = (id) => {
      setData( data.map( item => {
         if (item.id === id) item.plan.forEach( p => p.checked = !p.checked )
         return item
      } ) )
   }

   return (
      <section className="equipments">
         <div className="wrapper">
            <h1 className="equipments__title"></h1>

            <Slider {...settingsSlider} className="equipments__slider slider">

               {data.map( eq => (
                  <EqCard key={eq.id} eq={eq} handleChangePlan={handleChangePlan}/>
               ) )}

            </Slider>

         </div>
      </section>
   )
